<template>
  <div class="details">
    <div class="back">
      <van-nav-bar fixed @click-left="onClickLeft">
        <template #left>
          <van-icon name="cross" size="14" />
        </template>
      </van-nav-bar>
    </div>
    <div class="pro-img">
      <img class="auto-img" :src="productData.large_img" alt />
    </div>
    <div class="pro-info">
      <div class="pro-info-card">
        <h2 class="ch-name">
          {{productData.name}}(
          <span>{{productData.type_desc}}</span>)
        </h2>
        <div class="en-name">{{productData.enname}}</div>
        <div class="pro-selet" v-for="(item,index) in productData.rules" :key="index">
          <h2>{{item.title}}:</h2>
          <div class="pro-selet-tabs">
            <div
              class="pro-selet-tab"
              :class="{active: v.isActive}"
              @click="seletTab(v,index)"
              v-for="(v,ind) in item.rules"
              :key="ind"
            >{{v.title}}</div>
          </div>
        </div>
      </div>
      <div class="pro-info-card">
        <div class="pro-desc">
          <h2>商品详情：</h2>
          <p v-for="(item,index) in productData.desc" :key="index">{{index+1}}、{{item}}</p>
        </div>
      </div>
    </div>
    <div class="pro-btn">
      <van-goods-action>
        <div class="fl">
          <div class="btn-price">￥{{productData.price}}</div>
          <div class="btn-name">{{productData.name}}</div>
        </div>
        <div class="fr">
          <van-stepper v-model="count" theme="round" button-size="20" disable-input />
        </div>
      </van-goods-action>
      <van-goods-action>
        <van-goods-action-icon
          :color="isLike?'#0C34BA':'#cccccc'"
          @click="taggleLike"
          icon="like"
          text="喜欢"
        />
        <van-goods-action-icon
          icon="bag-o"
          text="购物袋"
          :badge="bagCount==0?'':bagCount"
          :to="{name:'Shopbag'}"
        />
        <van-goods-action-button color="#be99ff" type="warning" @click="addShopbag" text="加入购物袋" />
        <van-goods-action-button color="#0C34BA" type="danger" text="立即购买" @click="buyNow" />
      </van-goods-action>
    </div>
  </div>
</template>
<script>
import "../assets/css/details.less";
import { utils } from "../assets/js/utils";
// import { component } from 'vue/types/umd';
export default {
  data() {
    return {
      pid: "",
      count: 0,
      bagCount: 0,
      isLike: false,
      productData: {},
    };
  },
  created() {
    // 获取路由传过来的pid
    this.pid = this.$route.query.pid;

    //根据商品pid获取商品数据
    this.getProductData();

    //查询商品是否被收藏
    this.findLike();

    // 查询购物袋商品条数
    this.findShopbag();
  },
  methods: {
    // 获取商品数据
    getProductData() {
      // 数据加载等待动画
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      //   ajax
      this.axios({
        method: "GET",
        url: "/productDetail",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      }).then((result) => {
        //   关闭等待动画
        this.$toast.clear();
        //  
        if (result.data.code == 600) {
          //处理商品描述数据
          result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

          let ruleItems = ["tem", "sugar", "cream", "milk"];
          let rulesData = [];
          ruleItems.map((v) => {
            if (result.data.result[0][v] == "") {
              return;
            }

            let rule = {
              title: result.data.result[0][v + "_desc"],
              rules: [],
            };

            let r = result.data.result[0][v].split("/");
            r.map((value, index) => {
              let o = {
                title: value,
                isActive: index == 0,
              };
              rule.rules.push(o);
            });

            rulesData.push(rule);
          });

          result.data.result[0].rules = rulesData;

          this.productData = result.data.result[0];

           
        }
      });
    },

    //查询商品是否被收藏
    findLike() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          tokenString,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //  
          if (result.data.code == 1000) {
            if (result.data.result.length == 1) {
              this.isLike = true;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    // 查询购物袋商品条数
    findShopbag() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
           

          if (result.data.code == 5000) {
            this.bagCount = result.data.result.length;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    // 返回按钮
    onClickLeft() {
      this.$router.go(-1);
    },

    // 选择规格
    seletTab(v, index) {
       
      if (v.isActive) {
        return;
      }
      let ruleData = this.productData.rules[index].rules;
      for (let i = 0; i < ruleData.length; i++) {
        if (ruleData[i].isActive) {
          ruleData[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
       
    },

    // 喜欢点击事件
    taggleLike() {
      // 获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      // 数据加载等待动画
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
      });
       
      let url = this.isLike ? "/notlike" : "/like";

      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }
          this.$toast(result.data.msg);
          if (result.data.code == 800) {
            this.isLike = true;
          } else if (result.data.code == 900) {
            this.isLike = false;
          }
        })
        .catch((err) => {
           
        });
    },

    // 加入购物袋点击事件
    addShopbag() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      //获取选择的规格
      let rules = this.productData.rules;

       

      let rule = [];
      // 获取商品规格
      rules.map((v) => {
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            rule.push(v.rules[i].title);
            break;
          }
        }
      });

      //序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
        count: this.count,
        rule: rule.join("/"),
      });

       

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              //累加徽章数量, 购物车数据，而不是购物车商品总数量
              this.bagCount++;
            }
          }

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    // 立即购买
    buyNow() {
      // 加入购物袋,跳转到提交订单页面
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      //获取选择的规格
      let rules = this.productData.rules;

       

      let rule = [];
      // 获取商品规格
      rules.map((v) => {
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            rule.push(v.rules[i].title);
            break;
          }
        }
      });

      //序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
        count: this.count,
        rule: rule.join("/"),
      });

       

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              //累加徽章数量, 购物车数据，而不是购物车商品总数量
              this.bagCount++;
            }
          }
          let sids = [];
          sids.push(result.data.sid);
          // 序列化，数组转为字符串格式
          sids = JSON.stringify(sids);
           
          if (sids == "[]") {
            this.$toast("请勾择商品");
            return;
          }
          this.$router.push({ name: "Pay", query: { sids } });
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>